 


import React from 'react';
import Typewriter from "typewriter-effect";
import { Container, Row, Col } from 'react-bootstrap';
import './Banner.css';
import TrackVisibility from 'react-on-screen';
import headerImg from '../Assets/wall3.png';
 


// import wall from '../Assets/wall.jpg';
// import { useInView } from 'react-intersection-observer';

function Banner1() {

return (
    <section className="banner" id="home">
      <Container>
        <Row className="aligh-items-center">
          <Col xs={12} md={3} xl={6}>
            <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                <span className="tagline">Welcome to my Portfolio</span>
                <h1> Hi! I'm Poornima  
               <span>
                 <Typewriter
                   options={{
                     strings: ['< Front-End Dev />'],
                     autoStart: true,
                     loop: true,
                     delay: 300,
                     deleteSpeed: 100,
                     cursor: '|',
                     skipAddStyles: true,
                     
                   }}
                 />
               </span>
             </h1>  
                  <p>I thrive in the dynamic world of front-end development, where I merge creativity with functionality to deliver seamless user experiences.Throughout my journey, I've honed my skills in leveraging React.js to architect scalable and maintainable codebases.I have a strong grasp of modern web technologies such as ES6/ES7 and Bootstrap allowing me to stay at the forefront of industry best practices.
                  </p>
                  <p>I'm enthusiastic about leveraging my expertise in React development to contribute to transformative projects and drive meaningful impact.  I'm eager to join a team where I can not only apply my technical prowess but also thrive in a collaborative and growth-oriented environment.</p>
                  <p> I'm excited about the opportunity to bring my passion for React development to your team and contribute to its success.</p>
                  <button><a href="mailto:poornimapoyyamozhi22@gmail.com">Contact Me</a></button> 
                  {/* <button onClick={() => console.log('connect')}>Let’s Connect </button> */}
              </div>}
            </TrackVisibility>
          </Col>
          <Col xs={12} md={3} xl={5}>
            <TrackVisibility>
              {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__zoomIn" : ""}>
                  <img src={headerImg} alt="Header Img"/>
                </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
    </section>
  )
}
export default Banner1;