import { Container, Row, Col } from "react-bootstrap";
 import './Footer.css';
import navIcon1 from '../Assets/linkedin.svg';
 
import navIcon3 from '../Assets/instagram.svg';

  const Footer = () => {
  return (
    <footer className="footer">
      <Container>
        <Row className="align-items-center">
          
          <Col size={12} sm={6}>
            <h1>Poorni</h1>
          </Col>
          <Col size={12} sm={6} className="text-center text-sm-end">
            <div className="social-icon">
              <a href='https://www.linkedin.com/in/poornima2205'><img src={navIcon1} alt="Icon" /></a>
               
              <a href="https://www.instagram.com/miss__ninja__/"><img src={navIcon3} alt="Icon" /></a>
            </div>
            <p>Copyright 2022. All Rights Reserved</p>
          </Col>
        </Row>
      </Container>
    </footer>
  )
}
export default Footer;