import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import ProjectCard  from "./ProjectCart";
import projImg1 from "../Assets/Pro3.png";
import projImg2 from '../Assets/drum01.jpg'
import TrackVisibility from 'react-on-screen';
 
import './Project.css';

  const Project = () => {

  const Project = [
    {
      title: "Animate",
      description: "Ecommerce Website With ReactJS",
      imgUrl: projImg1,
      URL:'https://kawasakininja22.github.io/Animate'
    },
    {
      title: "Drum Kit",
      description: "Javascript",
      imgUrl: projImg2,
      URL:'https://brilliant-blini-ca33a2.netlify.app/'
    },
  
  ];

  return (
    <section className="project" id="projects">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn": ""}>
                <h2>Projects</h2>
                 
                <Tab.Container id="projects-tabs" defaultActiveKey="first">
                <div className="project-img">
                  <Tab.Content id="slideInUp" className={isVisible ? "animate__animated animate__slideInUp" : ""}>
                    <Tab.Pane eventKey="first" className="tabcontent">
                      <Row>
                        {
                          Project.map((project, index) => {
                            return (
                              <ProjectCard
                                key={index}
                                {...project}
                                />
                            )
                          })
                        }
                      </Row>
                    </Tab.Pane>
                   
                  
                  </Tab.Content>
                  </div>
                </Tab.Container>
              </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      {/* <img className="background-image-right" src={colorSharp2}></img> */}
    </section>
  )
}

export default Project;