import React from "react";
import { useState } from "react";
import { Link } from 'react-router-dom';
import {Navbar} from "react-bootstrap";
import './Nav.css';
import  linkin from './Assets/linkedin.svg';
import insta from './Assets/instagram.svg';
import 'bootstrap/dist/css/bootstrap.min.css';


   function Nav(){ 
    const [menu ,  setMenu] = useState('Home');

    return(
      <Navbar className="navbar navbar-expand-lg ">
       
      <div class="container-fluid md-6 xl-12" >
        <Navbar.Brand className="logo" href="/"> Poorni </Navbar.Brand>

        <div className="collapse navbar-collapse "  id="navbarSupportedContent">
        <div className="social-icon">
           <a href="https://www.linkedin.com/in/poornima2205/"><img src={linkin} alt="" /></a>
           <a href="https://www.instagram.com/miss__ninja__/"><img src={insta} alt="" /></a>
           </div>
           

        </div>
      </div>
     
    </Navbar>

   
)
    }
export default Nav;









